body {
  background-color: rgb(75, 80, 83);
  color: rgb(235, 23, blue);
}


*, *:before, *:after {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 16px;
}
.plane {
  margin: 20px auto;
  max-width: 150px;
}
.cockpit {
  height: 125px;
  position: relative;
  overflow: hidden;
  text-align: center;
  border-bottom: 0px solid #d8d8d8;
}
.cockpit:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 250px;
  width: 100%;
  border-radius: 100%;
  border: 4px solid rgb(0, 0, 0);
  background-color: rgb(130, 134, 137);
}
.cockpit h1 {
  width: 60%;
  margin: 100px auto 35px auto;
  
  background-color: rgb(130, 134, 137);
}


.cockpit_inv {
  height: 125px;
  position: relative;
  overflow: hidden;
  text-align: center;
  border-bottom: 0px solid #d8d8d8;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cockpit_inv:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 250px;
  width: 100%;
  border-radius: 25%;
  border: 4px solid rgb(0, 0, 0);
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  background-color: rgb(130, 134, 137);

}
.cockpit_inv h1 {
  width: 60%;
  margin: 100px auto 35px auto;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}


.exit {
  position: relative;
  height: 50px;
}
.exit:before, .exit:after {
  content: "EXIT";
  font-size: 14px;
  line-height: 18px;
  padding: 0px 2px;
  font-family: "Arial Narrow", Arial, sans-serif;
  display: block;
  position: absolute;
  background: green;
  color: white;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.exit:before {
  left: 0;
}
.exit:after {
  right: 0;
}
.fuselage {
  border-right: 4px solid rgb(0, 0, 0);
  border-left: 4px solid rgb(0, 0, 0);
  min-height: 500px;
  background-color: rgb(130, 134, 137);
}
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}
.seats {
  display: flex;  
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  margin-left: 5%;


  
}


.seat {
  margin-top: 15%;
  display: flex;
  flex: 0 0 45%;
  padding: 5px;
  position: relative;

}

.seat:nth-child(1) {
  margin-right: 7%;
}

.seat input[type=checkbox] {
  position: absolute;
  opacity: 0;
}
